import * as React from "react";
import { useTheme } from "@mui/material/styles";
import MobileStepper from "@mui/material/MobileStepper";
import Button from "@mui/material/Button";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import {
  Box,
  FormControlLabel,
  Radio,
  RadioGroup,
  TextField,
  FormLabel,
  MenuItem,
  InputLabel,
  Select,
} from "@mui/material";

import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";
import { MDBCard, MDBCardBody, MDBCol, MDBRow } from "mdb-react-ui-kit";
import ButtonGroup from "@mui/material/ButtonGroup";
import { useNavigate } from "react-router";
import { FormControl } from "react-bootstrap";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function DialogBox() {
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);

  const navigate = useNavigate();
  const goToRegisterPage = () => navigate("/register");

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const [open, setOpen] = React.useState(true);

  // const handleClickOpen = () => {
  //   setOpen(true);
  // };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
      {/* <Button variant="outlined" onClick={handleClickOpen}>
        Slide in alert dialog
      </Button> */}
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>{"What About you ? "}</DialogTitle>
        <DialogContent>
          <div>
            <Box sx={{ maxWidth: "100%", flexGrow: 1 }}>
              {activeStep === 0 ? (
                <div>
                  <MDBCard>
                    <MDBCardBody className="px-3">
                      <MDBRow>
                        <FormLabel
                          id="demo-row-radio-buttons-group-label"
                          style={{ marginLeft: "10px" }}
                        >
                          I am
                        </FormLabel>
                        <RadioGroup
                          row
                          aria-labelledby="demo-row-radio-buttons-group-label"
                          name="row-radio-buttons-group"
                        >
                          <FormControlLabel
                            value="female"
                            control={<Radio />}
                            label="Student"
                          />
                          <FormControlLabel
                            value="male"
                            control={<Radio />}
                            label="Parent"
                          />
                        </RadioGroup>
                      </MDBRow>
                      <br />
                      <MDBRow>
                        <MDBCol md="12">
                          <TextField
                            id="student-first-name"
                            label="Student First Name"
                            variant="outlined"
                            fullWidth
                          />
                        </MDBCol>
                      </MDBRow>
                      <br />
                      <MDBRow>
                        <MDBCol md="12">
                          <TextField
                            id="student-last-name"
                            label="Student Last Name"
                            variant="outlined"
                            fullWidth
                          />
                        </MDBCol>
                      </MDBRow>
                      <br />
                    </MDBCardBody>
                  </MDBCard>
                </div>
              ) : activeStep === 1 ? (
                <div>
                  <MDBCard>
                    <MDBCardBody className="px-3">
                      <MDBRow>
                        <MDBCol md="12">
                            <InputLabel id="high-school-graduation-completion-year-select-label">
                              Looking to Enroll in
                            </InputLabel>
                            <Select
                              labelId="high-school-graduation-completion-year-select-label"
                              id="high-school-graduation-completion-year-select"
                              label="high school graduation completion year"
                              fullWidth
                            >
                              <MenuItem value={1} defaultChecked>
                                Fall {new Date().getFullYear()}
                              </MenuItem>
                              <MenuItem value={2}>
                                Spring {new Date().getFullYear()}
                              </MenuItem>
                              <MenuItem value={3}>
                                Fall {new Date().getFullYear() + 1}
                              </MenuItem>
                              <MenuItem value={4}>
                                Spring {new Date().getFullYear() + 1}
                              </MenuItem>
                            </Select>
                        </MDBCol>
                      </MDBRow>
                      <br />
                      <MDBRow>
                        <MDBCol md="12">
                          <FormLabel>Enrolled As</FormLabel>
                          <ButtonGroup
                            fullWidth
                            orientation="vertical"
                            aria-label="vertical outlined button group"
                          >
                            <Button key="one">1 Year</Button>
                            <Button key="two">Transfer</Button>
                          </ButtonGroup>
                        </MDBCol>
                      </MDBRow>
                    </MDBCardBody>
                  </MDBCard>
                </div>
              ) : activeStep === 2 ? (
                <div>
                  <MDBCard>
                    <MDBCardBody>
                      <MDBRow>
                        <MDBCol md="12">
                          <FormLabel>Transfer Year</FormLabel>
                          <ButtonGroup
                            fullWidth
                            orientation="vertical"
                            aria-label="vertical outlined button group"
                          >
                            <Button key="one">Sophmore</Button>
                            <Button key="two">Junior</Button>
                            <Button key="two">Senior</Button>
                          </ButtonGroup>
                        </MDBCol>
                      </MDBRow>
                    </MDBCardBody>
                  </MDBCard>
                </div>
              ) : activeStep === 3 ? (
                <div>
                  <MDBCard>
                    <MDBCardBody>
                      <MDBRow>
                        <MDBCol md="12">
                          <FormLabel>Type of Degree</FormLabel>
                          <ButtonGroup
                            fullWidth
                            orientation="vertical"
                            aria-label="vertical outlined button group"
                          >
                            <Button key="one">Certificate</Button>
                            <Button key="two">Associate Degree</Button>
                            <Button key="two">Bachelor Degree</Button>
                          </ButtonGroup>
                        </MDBCol>
                      </MDBRow>
                    </MDBCardBody>
                  </MDBCard>
                </div>
              ) : (
                goToRegisterPage()
              )}
            </Box>
            <MobileStepper
              variant="dots"
              steps={4}
              position="static"
              activeStep={activeStep}
              sx={{ maxWidth: "100%", flexGrow: 1 }}
              nextButton={
                <Button size="small" onClick={handleNext}>
                  {activeStep < 3 ? "Next" : "Submit"}
                  {theme.direction === "rtl" ? (
                    <KeyboardArrowLeft />
                  ) : (
                    <KeyboardArrowRight />
                  )}
                </Button>
              }
              backButton={
                <Button
                  size="small"
                  onClick={handleBack}
                  disabled={activeStep === 0}
                >
                  {theme.direction === "rtl" ? (
                    <KeyboardArrowRight />
                  ) : (
                    <KeyboardArrowLeft />
                  )}
                  Back
                </Button>
              }
            />
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
}
