import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormLabel,
  FormGroup,
  InputLabel,
  Select,
  Radio,
  MenuItem,
  RadioGroup,
  Slider,
  Switch,
  TextField,
  
} from "@mui/material";

function DemographicInformation() {
  const [age, setAge] = React.useState("");
  const handleChange = (event) => {
    setAge(event.target.value);
  };

  const handleResidencyStatus = (event) => {
    if (event.target.value === 2) {
      document.getElementById("citenship_status").style.display = "block";
    } else {
      document.getElementById("citenship_status").style.display = "none";
    }
  };
  return (
    <div>
        <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
              Demographic Information
            </h3>
            <MDBRow>
              <MDBCol md="4">
                <TextField
                  id="student-first-name"
                  label="Student First Name"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-middle-name"
                  label="Student Middle Name"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-last-name"
                  label="Student Last Name"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="residential-status-select-label">
                    Residential Status :{" "}
                  </InputLabel>
                  <Select
                    labelId="residential-status-select-label"
                    id="residential-status-select"
                    label="Residential Status"
                    onChange={handleResidencyStatus}
                  >
                    <MenuItem value={1}>US Citizen or Permanent Resident</MenuItem>
                    <MenuItem value={2}>International</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="8" style={{ display: "none" }} id="citenship_status">
                <TextField
                  id="residential-status-non-us-text-field"
                  label="Citizenship Status"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
              <MDBCol md="12">
                <TextField
                  id="student-address-line1"
                  label="Street Address"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br/>
            <MDBRow>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label="City"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label="State"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="student-address-line1"
                  label="ZIP Code"
                  variant="outlined"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="6">
                <FormControl>
                  <FormLabel id="Ggender-label">
                    Gender
                  </FormLabel>
                  <RadioGroup
                    row
                    aria-labelledby="gender-label"
                    name="row-radio-buttons-group"
                  >
                    <FormControlLabel
                      value="female"
                      control={<Radio />}
                      label="Female"
                    />
                    <FormControlLabel
                      value="male"
                      control={<Radio />}
                      label="Male"
                    />
                    <FormControlLabel
                      value="Not Binary"
                      control={<Radio />}
                      label="Not Binary"
                    />
                    <FormControlLabel
                      value="Prefer Not to say"
                      control={<Radio />}
                      label="Prefer Not to Say"
                    />
                  </RadioGroup>
                </FormControl>
              </MDBCol>
              <MDBCol md="6">
                <FormControl fullWidth>
                  <InputLabel id="student-ethinic-origin-label">
                    Student's Ethinic Origin
                  </InputLabel>
                  <Select
                    labelId="student-ethinic-origin-label"
                    id="student-ethinic-origin"
                    label="StudentsEthinicOrigin"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Caucassian</MenuItem>
                    <MenuItem value={20}>African American</MenuItem>
                    <MenuItem value={30}>Hispanic</MenuItem>
                    <MenuItem value={30}>Two or more races</MenuItem>
                    <MenuItem value={30}>Unknown</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
    </div>
  );
}

export default DemographicInformation;