import React from "react";
import { MDBRow, MDBCol, MDBCard, MDBCardBody } from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  Switch,
  TextField,
  Stack,
  Autocomplete,
} from "@mui/material";

import Schools from "./Schools";

function AcademicProfile() {
  const [age, setAge] = React.useState("");
  const handleChange = (event) => {
    setAge(event.target.value);
  };

  const graduation_years = [];
  for (let index = 0; index < 7; index++) {
    graduation_years.push(new Date().getFullYear() + index);
  }

  return (
    <div>
      <MDBCard>
        <MDBCardBody className="px-4">
          <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
            Academic Profile
          </h3>
          <MDBRow>
            <MDBCol md="8">
              <Stack spacing={3}>
                <Autocomplete
                  multiple
                  id="tags-outlined"
                  options={Schools.HighSchools}
                  getOptionLabel={(option) => option.NAME}
                  filterSelectedOptions
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Name of High School"
                      placeholder="Name of High School"
                    />
                  )}
                />
              </Stack>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="high-school-graduation-completion-year-select-label">
                  High School Graduation (12th grade)/completion year
                </InputLabel>
                <Select
                  labelId="high-school-graduation-completion-year-select-label"
                  id="high-school-graduation-completion-year-select"
                  label="High School Graduation (12th grade)/completion year "
                >
                  {graduation_years.map((year) => (
                    <MenuItem>{year}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="looking-to-enroll-in-select-label">
                  Looking to Enroll in
                </InputLabel>
                <Select
                  labelId="looking-to-enroll-in-label"
                  id="looking-to-enroll-in-select"
                  label="looking-to-enroll-in"
                >
                  <MenuItem value={1}>Fall {new Date().getFullYear()}</MenuItem>
                  <MenuItem value={2}>
                    Spring {new Date().getFullYear()}
                  </MenuItem>
                  <MenuItem value={3}>
                    Fall {new Date().getFullYear() + 1}
                  </MenuItem>
                  <MenuItem value={4}>
                    Spring {new Date().getFullYear() + 1}
                  </MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="enrolled-as-select-label">
                  ENROLLED AS
                </InputLabel>
                <Select
                  labelId="enrolled-as-select-label"
                  id="enrolled-as-select"
                  value={age}
                  label="ENROLLED AS"
                  onChange={handleChange}
                >
                  <MenuItem value={10}>FIRST YEAR</MenuItem>
                  <MenuItem value={20}>TRANSFER</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="transfer-year-select-label">
                  Transfer Year
                </InputLabel>
                <Select
                  labelId="transfer-year-select-label"
                  id="transfer-year-select"
                  value={age}
                  label="Transfer year"
                  onChange={handleChange}
                >
                  <MenuItem value={10}>Sophmore</MenuItem>
                  <MenuItem value={20}>Junior</MenuItem>
                  <MenuItem value={30}>Senior</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br />

          <MDBRow>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="student-class-rank-select-label">
                  Student Class Rank (percentile)
                </InputLabel>
                <Select
                  labelId="student-class-rank-select-label"
                  id="student-class-rank-select"
                  value={age}
                  label="Student Class Rank (percentile)"
                  onChange={handleChange}
                >
                  <MenuItem value={10}>Top 10%</MenuItem>
                  <MenuItem value={20}>Top Quarter</MenuItem>
                  <MenuItem value={30}>Top half</MenuItem>
                  <MenuItem value={40}>Bottom Half</MenuItem>
                  <MenuItem value={50}>Bottom Quarter</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="gpa-weighted-type-label">
                  GPA Weightage Type
                </InputLabel>
                <Select
                  labelId="gpa-weighted-type-label"
                  id="gpa-weighted-type"
                  value={age}
                  label="GPA Weightage Type"
                  onChange={handleChange}
                >
                  <MenuItem value={1}>Weighted</MenuItem>
                  <MenuItem value={2}>UnWeighted</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <MDBRow>
                <MDBCol md="5">
                  <TextField
                    id="your-gpa-standard-basic"
                    type="number"
                    label="Your GPA"
                    variant="standard"
                    fullWidth
                  />
                </MDBCol>
                <MDBCol md="3">
                  <br/>
                  <p>Out of</p>
                </MDBCol>
                <MDBCol md="4">
                  <TextField
                    id="toatal-gpa-standard-basic"
                    type="number"
                    label="Total GPA"
                    variant="standard"
                    fullWidth
                  />
                </MDBCol>
              </MDBRow>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="4">
              <FormControl fullWidth>
                <InputLabel id="transfer-year-label">
                  Transfer Year
                </InputLabel>
                <Select
                  labelId="transfer-year-label"
                  id="transfer-year"
                  value={age}
                  label="Transfer year"
                  onChange={handleChange}
                >
                  <MenuItem value={10}>Certificate</MenuItem>
                  <MenuItem value={20}>Associate Degree</MenuItem>
                  <MenuItem value={30}>Bachelor's Degree</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <FormControl fullWidth>
                <p>
                  International Baccalauerate (IB)
                  <Switch labelId="international-baccalauerate-label" />
                </p>
              </FormControl>
            </MDBCol>
            <MDBCol md="4">
              <TextField
                id="predicted-ib-score"
                type="number"
                label="Predicted IB Score (Out of 45)"
                variant="standard"
                fullWidth
              />
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="2">
              <FormControlLabel
                control={<Checkbox />}
                label="PSAT SCORE "
                labelPlacement="end"
                id="psat_score_enable"
              />
            </MDBCol>
            <MDBCol md="4">
              <p>
                PSAT Composite :
                <Slider
                  max={"760"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
            <MDBCol md="2">
              <FormControlLabel
                control={<Checkbox />}
                label="SAT SCORE "
                labelPlacement="end"
                id="sat_score_enable"
              />
            </MDBCol>
            <MDBCol md="4">
              <p>
                SAT Composite :
                <Slider
                  max={"760"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="3">
              <p>
                PSAT VERBAL :
                <Slider
                  max={"760"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
            <MDBCol md="3">
              <p>
                PSAT MATHS :
                <Slider
                  max={"760"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
            <MDBCol md="3">
              <p>
                SAT VERBAL :
                <Slider
                  max={"760"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
            <MDBCol md="3">
              <p>
                SAT MATHS :
                <Slider
                  max={"760"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="2">
              <FormControlLabel
                control={<Checkbox />}
                label="ACT SCORE "
                labelPlacement="end"
                id="psat_score_enable"
              />
            </MDBCol>
            <MDBCol md="4">
              <p>
                Act Composite :
                <Slider
                  max={"36"}
                  aria-label="Default"
                  valueLabelDisplay="auto"
                />
              </p>
            </MDBCol>
            <MDBRow>
              <MDBCol md="3">
                <p>
                  ACT VERBAL :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT MATHS :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT ENGLISH :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT SCIENCE :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
          </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </div>
  );
}
export default AcademicProfile;
