import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControlLabel,
  FormLabel,
  FormGroup,
  Radio,
  RadioGroup,
  Slider,
  Switch,
  TextField,
} from "@mui/material";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Autocomplete from "@mui/material/Autocomplete";
import Stack from "@mui/material/Stack";
import GeoprahicPreference from "./GeopraphicPreference/GeoprahicPreference";
import PercentSlide from "./PercentageSliders/PercentSlide";

function PreferenceMotivation() {
  const [age, setAge] = React.useState("");
  const [satScore, setSatScore] = useState(false);
  const [affinity, setAffinity] = useState(false);
  const [affordability, setAffordability] = useState(false)
  const [addmisionability, setAddmissionability] = useState(false)


  const handleChange = (event) => {
    setAge(event.target.value);
  };

  const affinityPercentShow = () =>{
    if(affinity == false){
      document.getElementById('affinity-percent').style.display='block';
      setAffinity(true);
    } else{
      document.getElementById('affinity-percent').style.display='none';
      setAffinity(false);
    }
  }
  const affordalityPercentShow = () =>{
    if(affordability == false){
      document.getElementById('affordability-percent').style.display='block';
      setAffordability(true);
    } else{
      document.getElementById('affordability-percent').style.display='none';
      setAffordability(false);
    }
  }
  const addmisabilityPercentShow = () =>{
    if(addmisionability == false){
      document.getElementById('addmisionability-percent').style.display='block';
      setAddmissionability(true);
    } else{
      document.getElementById('addmisionability-percent').style.display='none';
      setAddmissionability(false);
    }
  }

  const handleResidencyStatus = (event) => {
    if (event.target.value === 2) {
      document.getElementById("citenship_status").style.display = "block";
    } else {
      document.getElementById("citenship_status").style.display = "none";
    }
  };

  const college = [
    { title: "Public" },
    { title: "Private Non-Profit" },
    { title: "Private Profit" },
  ];

  const fields = [
    { title: "Engineering Mechanics" },
    { title: "Electro Mechanical Engineering" },
    { title: "Liberal And Arts" },
  ];

  const displayItem = () => {
    var sat = document.getElementById("psat_score_enable");

    console.log("asfd");
  };

  return (
    <div>
      <MDBCard>
        <MDBCardBody className="px-4">
          <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
            Preferences and Motivations
          </h3>
          <MDBRow>
            <MDBCol md="4">
              <Stack spacing={3}>
                <Autocomplete
                  multiple
                  id="preferences-and-motivations"
                  options={college}
                  getOptionLabel={(option) => option.title}
                  filterSelectedOptions
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="College Preference"
                      placeholder="College Preference"
                    />
                  )}
                />
              </Stack>
            </MDBCol>
            <MDBCol md="4">
              <Stack spacing={3}>
                <Autocomplete
                  multiple
                  id="planned-field-of-study"
                  options={fields}
                  getOptionLabel={(option) => option.title}
                  filterSelectedOptions
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Planned Field of Study"
                      placeholder="Planned Field of Study"
                    />
                  )}
                />
              </Stack>
            </MDBCol>
            <MDBCol md="4">
              <GeoprahicPreference />
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="6">
              <FormControl component="fieldset">
                <FormLabel component="legend">School Size</FormLabel>
                <FormGroup aria-label="position" row>
                  <FormControlLabel
                    value="Affinity - perceived level of fit with the school"
                    control={<Checkbox />}
                    label="SMALL"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Affordability - whether I/we can afford it"
                    control={<Checkbox />}
                    label="MEDIUM"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Admissibility and Academics - reputation and my chances of getting in"
                    control={<Checkbox />}
                    label="LARGE"
                    labelPlacement="end"
                  />
                </FormGroup>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="religious-affiliation-select-label">
                  Religious Affliations
                </InputLabel>
                <Select
                  labelId="religious-affiliation-select-label"
                  id="religious-affiliation-select"
                  value={age}
                  label="type"
                  onChange={handleChange}
                  fullWidth
                >
                  <MenuItem value={10}>Very Important</MenuItem>
                  <MenuItem value={10}>Somewhat Important</MenuItem>
                  <MenuItem value={10}>Not At all Important</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <MDBRow>
            <MDBCol md="6">
              <FormControl component="fieldset">
                <FormLabel component="legend">Urbanicity</FormLabel>
                <FormGroup aria-label="position" row>
                  <FormControlLabel
                    value="Affinity - perceived level of fit with the school"
                    control={<Checkbox />}
                    label="City"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Affordability - whether I/we can afford it"
                    control={<Checkbox />}
                    label="SubUrban"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Admissibility and Academics - reputation and my chances of getting in"
                    control={<Checkbox />}
                    label="Town"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Admissibility and Academics - reputation and my chances of getting in"
                    control={<Checkbox />}
                    label="Rural"
                    labelPlacement="end"
                  />
                </FormGroup>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <FormControl fullWidth>
                <InputLabel id="specialized-mission-select-label">
                  Specialized Mission
                </InputLabel>
                <Select
                  labelId="specialized-mission-select-label"
                  id="specialized-mission-select"
                  value={age}
                  label="type"
                  onChange={handleChange}
                >
                  <MenuItem value={10}>Very Important</MenuItem>
                  <MenuItem value={10}>Somewhat Important</MenuItem>
                  <MenuItem value={10}>Not At all Important</MenuItem>
                </Select>
              </FormControl>
            </MDBCol>
          </MDBRow>
          <br />
          <MDBRow>
            <MDBCol md="6">
              <FormControl component="fieldset">
                <FormLabel component="legend">
                  What are the main reasons to attend an undergraduate college?
                </FormLabel>
                <FormGroup aria-label="position">
                  <FormControlLabel
                    value="Good job/maximize income upon graduation"
                    control={<Checkbox/>}
                    label="Good job/maximize income upon graduation"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Kinship/Making friends"
                    control={<Checkbox />}
                    label="Kinship/Making friends"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Obtain a credential to pursue graduate education"
                    control={<Checkbox />}
                    label="Obtain a credential to pursue graduate education"
                    labelPlacement="end"
                  />
                  <FormControlLabel
                    value="Other"
                    control={<Checkbox />}
                    label="Other"
                    labelPlacement="end"
                  />
                  <div style={{display: 'none'}}>
                  <MDBCol md="8" id="other">
                    <TextField
                      id="residential-status-non-us-text-field"
                      label="Reason?"
                      variant="standard"
                      fullWidth
                    />
                  </MDBCol>
                  </div>
                </FormGroup>
              </FormControl>
            </MDBCol>
            <MDBCol md="6">
              <FormControl component="fieldset">
                <FormLabel component="legend">
                  What are your primary considerations when selecting a college?
                </FormLabel>
                <FormGroup aria-label="position">
                  <FormControlLabel
                    value="Affinity - reputation/ranking, campus safety, transporation access, weather, and other related factors"
                    control={<Checkbox onChange={affinityPercentShow}/>}
                    label="Affinity - reputation/ranking, campus safety, transporation access, weather, and other related factors"
                    labelPlacement="end"
                  />
                  <div id='affinity-percent' style={{display: 'none'}}>
                     <PercentSlide/>
                  </div>
                  <FormControlLabel
                    value="Affordability - whether I/we can afford it"
                    control={<Checkbox onChange={affordalityPercentShow}/>}
                    label="Affordability - whether I/we can afford it"
                    labelPlacement="end"
                  />
                  <div id='affordability-percent' style={{display: 'none'}}>
                     <PercentSlide/>
                  </div>
                  <FormControlLabel
                    value="Admissibility - chances of getting in"
                    control={<Checkbox onChange={addmisabilityPercentShow}/>}
                    label="Admissibility -  chances of getting in"
                    labelPlacement="end"
                  />
                  <div id='addmisionability-percent' style={{display: 'none'}}>
                     <PercentSlide/>
                  </div>
                </FormGroup>
              </FormControl>
            </MDBCol>
          </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </div>
  );
}
export default PreferenceMotivation;
